<template>
  <div>
    

    <div class="container-fluid">
        <div class="row">
            <div class="col-md-2 ">
              <input type="text" class="  bg-light pb-0" syle="width:60%;">
              <button type="button" class="btn btn-secondary btn-sm">GO!</button></div>
            <div class="col-md-10 text-right"><button type="button" class="btn btn-secondary" @click="openModal(true)"><i class="fa fa-shopping-cart "></i>新增產品</button></div>
        </div>

   </div>
    
    <div class="container-fluid">
       <table class="table">
             <thead>
                     <tr>
                         
                          <th width="900">分類</th>
                          <th width="300">標題</th>
                          <th width="300">數量</th>
                          <th width="300">原價</th>
                          <th>售價</th>
                          <th>操作</th>
                     </tr>
             </thead>
             <tbody>
                    
                     <tr v-for="item in products" :key="item.id">
                        
                       <td>{{item.category}}</td>    
                       <td>{{item.title}}</td> 
                       <td>{{item.num}}</td> 
                       <td>{{item.origin_price}}</td> 
                       <td>{{item.price}}</td> 
                       <td>
                         <div class="btn-group" role="group" aria-label="Basic example">
                         <button type="button" class="btn btn-secondary"  @click="openModal(false,item)">編輯</button>
                        
                         <button type="button" class="btn btn-danger dropdown-toggle dropdown-toggle-split" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                           其他
                           <span class="sr-only">其他</span>
                         </button>
                         <div class="dropdown-menu">
                           <div class="dropdown-divider"></div>
                           <a class="dropdown-item" href="#" @click="delModal(item)" >刪除</a>
                         </div>
                       </div>
                     </td> 
                 </tr>


             </tbody>




       </table>
    </div>


    <div class="modal" tabindex="-1" role="dialog" id="modal3">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Modal title</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="container">
                 <div class="row">
                      <div class="col-sm-4">
                           <div>
                               <label>輸入圖片網址</label>
                               <input type="text" class="form-control" v-model="tempProduct.imageUrl">
                                
                           </div>
                           <div>
                            <label>或上傳圖片</label><i class="fas fa-spinner fa-spin" v-if=" status.uploading" ></i>
                            <input type="file" class="form-control" ref="files" @change="uploadFile">
                             
                        </div>

                      </div>
                      <div class="col-sm-8">
                        <div>
                          <label>標題</label>
                          <input type="text" class="form-control" v-model="tempProduct.title">
                        </div>
                           <div class="row">
                              <div class="col-sm-6">
                                <label>分類:</label>
                                <input type="text" class="form-control" v-model="tempProduct.category">
                              </div>
                              <div class="col-sm-6">
                                <label>單位:</label>
                                <input type="text" class="form-control" v-model="tempProduct.unit">
                              </div>

                           </div>
                           <div class="row">
                            <div class="col-sm-6">
                              <label>原價:</label>
                              <input type="text" class="form-control" v-model="tempProduct.origin_price">
                            </div>
                            <div class="col-sm-6">
                              <label>售價:</label>
                              <input type="text" class="form-control" v-model="tempProduct.price">
                            </div>

                         </div>

                         <div class="row">
                          <div class="col-sm-12">
                            <label>產品描述:</label>
                           <textarea class="form-control"   v-model="tempProduct.description"></textarea>
                          </div>
                          <div class="col-sm-12">
                            <label>說明內容:</label>
                            <textarea class="form-control" v-model="tempProduct.content"></textarea>
                          </div>

                       </div>
                       <input type="checkbox">是否啟用



                         
                     
                        
                      </div>




                 </div>
            </div>

          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button type="button" class="btn btn-primary" @click="updateProduct">Save changes</button>
          </div>
        </div>
      </div>
    </div>






<div class="modal" tabindex="-1" role="dialog" id="modal4">
  <div class="modal-dialog" role="document">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title">Modal title</h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
        <p>確定要刪除{{tempProduct.title}}</p>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
        <button type="button" class="btn btn-primary" @click="delProduct">確定</button>
      </div>
    </div>
  </div>
</div>








  </div>
</template>

<script>
import $ from 'jquery'
export default {
  name: 'HelloWorld',
  data () {
    return {
              products:[],
             isNew:false,
             tempProduct:{},
             status:{uploading:false}
             
    }
  },
  methods:{
              getProducts(page=1){
                     const api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/admin/products?page=${page}`            
                     const vm=this;
                     this.$http.get(api).then((response)=>{
                         console.log(response.data)
                         vm.products=response.data.products;
                     })

              },openModal(isNew,item){
                                         if(isNew){
                                                  this.tempProduct={}
                                                  this.isNew=true;
                                         }  else{
                                                 this.tempProduct=Object.assign({},item);
                                                  this.isNew=false;
                                         }   
                                         $("#modal3").modal("show"); 
              },updateProduct(){
                                 const vm=this;
                                 let api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/admin/product`
                                 let httpMethod="post"
                                 if(!vm.isNew){
                                                     api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`
                                                    httpMethod="put"
                                 }
                                 this.$http[httpMethod](api,{data:vm.tempProduct}).then((response)=>{

                                     $("#modal3").modal("hide");
                                     console.log(response);
                                 })
                               
              },delModal(item){
                                 this.tempProduct=Object.assign({},item);
                               $("#modal4").modal("show");



              },
              
              
              
              delProduct(){
                             const vm=this;
                             const api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`            
                             
                             this.$http.delete(api).then((response)=>{
                              console.log(response)
                                $("#modal4").modal("hide");
                     })
              },uploadFile(){
                                console.log(this);
                               const uploadFile=this.$refs.files.files[0];
                              const formData=new FormData();
                              formData.append("file-to-upload",uploadFile);
                               const vm=this;
                              vm.status.uploading=true;
                             const api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/admin/upload` 
                             
                             this.$http.post(api,formData,{headers:{'Content-Type':'multipart/form-data'}
                             
                              
                             
                             }).then((response)=>{
                               vm.status.uploading=false;
                                 this.$set(vm.tempProduct,"imageUrl",response.data.imageUrl)
                             })


                              

              }
  },
  created(){
            this.getProducts();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>  


