<template>
  <div>
    
    <h1 class="text-center mt-5" style="color:darkgray">六角血拚結帳</h1>
    <div class="container">
        <div class="row">
           <div class="col-md-4 "> <div class="alert alert-primary" role="alert" style="border-radius: 20px;">1.輸入訂單資料</div></div>
           <div class="col-md-4 "> <div class="alert alert-secondary" role="alert" style="border-radius: 20px;">2.金流付款</div></div>
           <div class="col-md-4 "> <div class="alert " role="alert" style="border-radius: 20px;">3.完成</div></div>
           
      </div>
    </div>
<div class="container">
    <h2 class="text-center">個人資料</h2>

    <table class="table">
       <tbody>
               <tr>
                    <th>Email</th>
                    <td>{{order.user.email}}</td>
               </tr>
               <tr>
                    <th>姓名</th>
                    <td>{{order.user.name}}</td>
               </tr>
               <tr>
                  <th>電話</th>
                  <td>{{order.user.tel}}</td>
              </tr>
              <tr>
                  <th>地址</th>
                  <td>{{order.user.address}}</td>
             </tr>
       </tbody>
    </table>
    <div>
      付款狀態
            <div v-if="!order.is_paid" class="text-primary">尚未付款</div>
             <div v-else class="text-danger">已付款</div>
    </div>
    <div v-if="order.is_paid==false">
    
    <button type="button" class="btn btn-outline-primary" @click="payOrder">確認付款去</button>
    </div>
    



</div>


  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
              orderId:'',
              order:{
                    user:{
                          address: "",
                          email: "",
                          name: "",
                          tel: ""
                    }
              }
    }
  },methods:{
               getOrder(){
                           const vm=this;
                            const api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/order/${vm.orderId} `
                                                                            
                            
                             this.$http.get(api).then((response)=>{
                              console.log(response.data)
                              vm.order=response.data.order;
                     })
               },
               payOrder(){
                           const vm=this;
                            const api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/pay/${vm.orderId} `
                                                                            
                            
                             this.$http.post(api).then((response)=>{
                              console.log(response.data)
                              this.getOrder();
                             })



               }


              
  },created(){
         this.orderId=this.$route.params.orderId;
         this.getOrder();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 .cart-btn .badge{
           position: absolute;
           left:-1px;
           top:-1px;
        }
        .bg-cover{
                  background-position: center;
                  background-size: cover;
        }
        .bg-lighter{
                 background-color: rgba(255,255,255, 0.45);
        }
</style>


