<template>
  <div>
    <nav class="navbar navbar-expand-lg navbar-light bg-light">
      <a class="navbar-brand" href="#">Navbar</a>
      <div class="dropdown  ml-auto">
        <button type="button" class="btn cart-btn" data-toggle="dropdown" >
          <i class="fa fa-shopping-cart text-dark fa-2x" aria-hidden="true" ></i>
          <span class="badge badge-pill badge-danger">{{cart.qty}}</span>
        </button>
        <div class="dropdown-menu dropdown-menu-right" style="min-width:300px">
          <form class="px-4 py-3">
               <h3>已選擇商品</h3>
               <table class="table">
                 <tbody>
                  <tr v-for="item in cart.carts" :key="item.id">
                    <th> <button type="button"> <i class="fa fa-trash-o" aria-hidden="true" @click="removeCartItem(item.id)"></i></button>   </th>
                    <td>{{item.product.title}}</td>
                    <td>{{item.product.num}}</td>
                    <td>${{item.product.price*item.qty}}</td>

                </tr>
                
                 </tbody>
               </table>
               <a class="btn btn-outline-primary btn-block"  href="#ss">結帳去</a>
            </form>
         </div>
      </div>
      
    </nav>
    
    <div class="jumbotron jumbotron-fluid bg-cover d-flex align-items-end" style="background-image:url(images/景觀\(1920-1280\).jpg);min-height: 400px;">
      <div class="container   ">
        <div class="bg-lighter ">
          <h1 class="display-4">范范旅館</h1>
          <p class="lead">坐卧在都市鬧區的一間小旅館</p>
        </div>
        
      </div>
    </div>

  <div class="container">
   <div class="row">
       <div class="col-md-3">
        <div class="list-group" id="myList" role="tablist">
          <a class="list-group-item list-group-item-action active" data-toggle="list" href="#home" role="tab">Home</a>
          <a class="list-group-item list-group-item-action" data-toggle="list" href="#profile" role="tab">Profile</a>
          <a class="list-group-item list-group-item-action" data-toggle="list" href="#messages" role="tab">Messages</a>
          <a class="list-group-item list-group-item-action" data-toggle="list" href="#settings" role="tab">Settings</a>
        </div>
        
       

       </div>
       <div class="col-md-9">
        <div class="tab-content">
          <div class="tab-pane active" id="home" role="tabpanel"><div class="row">
            <div class="col-md-4" v-for="item in products" :key="item.id">
              <div class="card h-100">
                <img class="card-img-top" :src="item.imageUrl" alt="Card image cap">
                <div class="card-body">
                  <h5 class="card-title">{{item.title}}</h5>
                  <p class="card-text">${{item.price}}</p>
                </div>
                <div class="card-footer">
                  <div class="btn-group" role="group" aria-label="Basic example">
                    <button type="button" class="btn btn-primary"  @click="getProduct(item.id)">了解更多</button>
                   
                    <button type="button" class="btn btn-secondary" @click="addtoCart(item.id)"> 加入購物車</button>
                    
                  </div>
                </div>
              </div>
            </div>
         </div>
         </div>
          <div class="tab-pane" id="profile" role="tabpanel">...</div>
          <div class="tab-pane" id="messages" role="tabpanel">...</div>
          <div class="tab-pane" id="settings" role="tabpanel">...</div>
        </div>
      </div>
   </div>
  </div>

<div class="container">
  <div class="accordion" id="accordionExample">
    <div  class="card">
               <div class="card-header" id="headingOne">
                  <h5 class="mb-0">
                     <button class="btn btn-link" type="button" data-toggle="collapse" data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                         顯示購物車細節
                   </button>
                 </h5>
               </div>
  
      <div id="collapseOne" class="collapse show" aria-labelledby="headingOne" data-parent="#accordionExample">
           <table class="table">
               <thead>
                     <tr>
                           <th class="text-center">商品名稱</th>
                           <th>數量</th>
                           <th>小計</th>
                       
                     </tr>

               </thead>
               <tbody >
                <tr v-for="item in cart.carts" :key="item.id">
                      <td><img :src="item.product.imageUrl" class="img-fluid" style="width:64px;height: 64px;"></td>
                      <td>{{item.product.title}}</td>
                    <td>{{item.qty}}</td>
                    <td>${{item.product.price*item.qty}}</td>
                     
                  
                </tr>
                
              </tbody>
              <tfoot >
                        <tr>
                        <td class=" text-right" colspan="4"></td>
                     </tr>
                     <tr>
                         <td class=" text-right" colspan="4">合計${{cart.total}}</td>
                     </tr>

                     
              </tfoot>
           </table>

 </div>

      </div>
    </div>
 </div>


    <form>
  <div class="form-row" id="ss">
    <div class="form-group col-md-6">
      <label for="inputEmail4">Email</label>
      <input type="email" class="form-control" id="inputEmail4" placeholder="Email" v-model="form.user.email">
    </div>
    <div class="form-group col-md-6">
      <label for="inputPassword4">Password</label>
      <input type="password" class="form-control" id="inputPassword4" placeholder="Password" >
    </div>
  </div>
  <div class="form-group">
    <label for="inputAddress">Address</label>
    <input type="text" class="form-control" id="inputAddress" placeholder="請輸入地址" v-model="form.user.address">
  </div>
  <div class="form-group">
    <label for="inputAddress2">姓名</label>
    <input type="text" class="form-control" id="inputAddress2" placeholder="請輸入姓名" v-model="form.user.name">
  </div>
  <div class="form-row">
    <div class="form-group col-md-6">
      <label for="inputCity">電話</label>
      <input type="tel" class="form-control" id="inputCity" placeholder="請輸入電話" v-model="form.user.tel">
    </div>
    
  </div>

  <button type="submit" class="btn btn-primary" @click="createOrder">提交</button>
</form>



<div class="modal" tabindex="-1" role="dialog" id="modals">
  <div class="modal-dialog" role="document">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title">{{product.title}}</h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
        <img :src="product.imageUrl" class="img-fluid" height="250">
        <div class="text-left">{{product.title}}</div>
        <div class="text-right">售價{{product.price}}</div>
         <div class="text-left">原價{{product.origin_price}}</div>
         <select v-model="product.num" class="form-control">
                 <option :value="num" v-for="num in 10" :key="num">選購{{num}}單位</option>


         </select>
      </div>
      <div class="modal-footer">
        <span>小計{{product.price*product.num}}元</span>
        <button type="button" class="btn btn-primary" @click="addtoCart(product.id,product.num)">確定</button>
      </div>
    </div>
  </div>
</div>



  </div>
</template>

<script>
import $ from 'jquery'
export default {
  name: 'HelloWorld',
  data () {
    return {
            products:[],
            product:{},
            cart:{},
            form:{ 
                   user:{
                           name: "",
                           email: "",
                           tel: "",
                          address: ""
                        },
                         message:''
                  }
    }
  },
  methods:{
             getProducts(page=1){
                     const api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/products?page=${page}`            
                     const vm=this;
                     this.$http.get(api).then((response)=>{
                         console.log(response.data)
                         vm.products=response.data.products;
                     })

              },
              getProduct(id){
                               const api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/product/${id}`  
                                                                                                      
                              const vm=this;
                              this.$http.get(api).then((response)=>{
                              console.log(response.data)
                              vm.product=response.data.product;
                              
                              $("#modals").modal("show");
                     })
                            

              },
              addtoCart(id,qty=1){

                           const api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/cart`  
                                                                                                      
                              const vm=this;

                             const form={
                                         product_id:id,
                                         qty
                             }
                              this.$http.post(api,{data:form}).then((response)=>{
                              console.log(response.data)
                               this.getCart();
                               $("#modals").modal("hide");
                      })

              },getCart(){
                           const api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/cart`  
                                                                                                      
                              const vm=this;
                              this.$http.get(api).then((response)=>{
                              console.log(response.data)
                              vm.cart=response.data.data;
                             
                              
                      })


              },removeCartItem(id){
                                          const api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/cart/${id}`  
                                                                 
                              const vm=this;
                              this.$http.delete(api).then((response)=>{
                              console.log(response.data)
                              
                              
                      })



              },createOrder(){

                    const api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/order`  
                                                                 
                              const vm=this;
                              this.$http.post(api,{data:vm.form}).then((response)=>{
                              console.log(response.data);
                              
                              vm.$router.push(`/customerCheckout/${response.data.orderId}`)


               })
              }
},
created(){
            this.getProducts();
            this.getCart()
        }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 .cart-btn .badge{
           position: absolute;
           left:-1px;
           top:-1px;
        }
        .bg-cover{
                  background-position: center;
                  background-size: cover;
        }
        .bg-lighter{
                 background-color: rgba(255,255,255, 0.45);
        }
</style>


