<template>
  <div>
              <table class="table">
                <thead>
                     <tr>
                       <th>訂單id</th>
                       <th>email</th>
                       <th>購買款項</th>
                       <th>金額</th>
                       <th>是否付款</th>
                  </tr>


                </thead>
                <tbody>
                         <tr v-for="item in orders" :key="item.id">
                               <td>{{item.create_at}}</td>
                               <td>{{item.user.email}}</td>
                               <td> <div v-for="item2 in item.products" :key="item2.id">{{item2.product.title}}x{{item2.qty}} </div></td>
                               <td>{{item.total}}元</td>
                               <td><div v-if="item.is_paid">已付款</div><div v-else>未付款</div></td>
                       </tr>
              </tbody>
                  
              </table>




  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
             orders:[]
    }
  },methods:{
             getmenu(page=1){
                              const api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/orders?page=${page}`            
                     const vm=this;
                     this.$http.get(api).then((response)=>{
                         console.log(response.data)
                         vm.orders=response.data.orders;
                     })
             }
             
  },
  created(){
               this.getmenu();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



